html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  background-color: var(--color-white);
  color: #fff;
}

a {
  text-decoration: none;
  color: inherit;
}

p {
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1600px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  color: #fff;
  position: fixed;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 0px 15px;
}

.header__logo {
  display: flex;
  align-items: center;
  z-index: 100;
}

.header__menu-link {
  display: inline-block;
  text-transform: uppercase;
  font-size: 15px;
  margin-left: 30px;
  position: relative;
}

.header__menu-link :after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  bottom: -5px;
  width: 0;
  height: 2px;
  background-color: #e83a1c;
  transition: width 0.3s ease-out;
}

.header__menu-link :hover:after {
  width: 100%;
}

.header__btn {
  display: flex;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  color: #fff;
  letter-spacing: 0.16px;
  text-transform: uppercase;
  filter: brightness(90%);
}

.header__btn-login {
  border: 3px solid #fff;
  border-radius: 30px;
  padding: 10px 12px;
  transition: all .1s ease-in;
}

.header__btn-login:hover {
  background-color: #fff;
  color: #000;
}

.header__btn-reg {
  margin-left: 15px;
  border: 3px solid #fff;
  border-radius: 30px;
  padding: 10px 12px;
}

.header__btn-reg:hover {
  background-color: #fff;
  color: #000;
}

.header__btn-mob {
  display: none;
}

.header-mobile {
  display: none;
  padding: 30px 0;
  background-color: #000;
}

.header-menu {
  transform: translateX(-150%);
}

.toggle-sidebar {
  display: none;
}

.menu-icon-wrapper {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.menu-icon {
  position: relative;
  width: 30px;
  height: 5px;
  background-color: #fff;
  border: none;
  border-radius: 15px;
}

.menu-icon::before {
  position: absolute;
  left: 0;
  top: -10px;
  content: '';
  width: 30px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
  border-radius: 15px;
}

.menu-icon::after {
  position: absolute;
  left: 0;
  top: 10px;
  content: '';
  width: 30px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
  border-radius: 15px;
}

.menu-icon.menu-icon-active,
.menu-icon.active {
  background-color: transparent;
}

.menu-icon.menu-icon-active::before,
.menu-icon.active::before {
  transform: rotate(45deg);
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.menu-icon.menu-icon-active::after,
.menu-icon.active::after {
  transform: rotate(-45deg);
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.sidebar.active {
  transform: translate(0);
}

.hero {
  width: 100%;
  min-height: 100vh;
  background-image: url(../img/main-bg.png);
  background-position: bottom right 65%;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #000;
  background-size: 70%;
  animation-name: home-fade;
  animation-duration: 1s;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-delay: 3s;
}

.hero__inner {
  display: flex;
  justify-content: space-between;
  padding: 250px 35px 100px 0;
}

.hero__info {
  max-width: 600px;
  width: 100%;
}

.hero__title {
  text-transform: uppercase;
  margin-bottom: 15px;
  font-size: 60px;
  max-width: 450px;
}

.hero__title span {
  color: #e83a1c;
  font-weight: 700;
}

.hero__info-text {
  font-size: 22px;
  font-weight: 200;
  line-height: 40px;
  margin-bottom: 50px;
}

.hero__info-text span {
  color: #e83a1c;
  font-weight: 700;
}

.hero__info-reg {
  display: inline-block;
  width: 250px;
  font-size: 22px;
  font-weight: 700;
  color: #fff;
  text-align: center;
  border: none;
}

.hero__banner {
  position: relative;
  width: 40%;
  min-height: 600px;
  perspective: 1000px;
  transform-style: preserve-3d;
}

.hero__banner-wrapper {
  width: 100%;
  height: 100%;
  perspective: 1000px;
  transform-style: preserve-3d;
  transition: transform .3s;
}

.hero__banner-img {
  position: absolute;
  pointer-events: none;
  transition: all .6s;
  border-radius: 8px;
  border: 1px solid #fff;
}

input[type="button"] {
  background-color: #e83a1c;
  height: 55px;
  border: none;
  outline: none;
  padding: 0 40px;
  color: #fff;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
}

input[type="button"]:hover {
  opacity: .9;
}

@keyframes blick-slide {
  from {
    background-position-x: -250px;
  }
  to {
    background-position-x: calc(100% + 250px);
  }
}

.blick-slide input {
  background-image: linear-gradient(-45deg, transparent 0%, transparent 49%, #fff 50%, transparent 70%) !important;
  background-repeat: no-repeat !important;
  animation: blick-slide infinite 4s;
}

