.brands {
  background-color: rgba(0, 0, 0, 0.897);
  padding: 50px 0 20px 0;
}

.brands__inner {
  overflow: hidden;
  background-image: linear-gradient(180deg, rgba(28, 28, 28, 0) 0%, #1c1c1c 40%);
}

.brands__title {
  font-size: 46px;
  font-weight: 200;
  line-height: 64px;
  text-align: center;
  text-transform: uppercase;
