.brands__content-header {
  display: flex;
  justify-content: space-around;
  margin: 0 30px;
  padding: 12px;
  border-radius: 55px;
  background-color: rgba(0, 0, 0, 0.35);
  margin-bottom: 45px;
}

.brands__slider-item {
  display: none;
