  display: inline-block;
  height: 60px;
  padding: 0;
  color: #fff;
  letter-spacing: 0.84px;
  text-align: center;
  text-transform: uppercase;
  border: none;
  border-radius: 30px;
  background-color: #e83a1c;
  filter: brightness(100%);
}

.visible {
  display: block !important;
}

.gallery-thumbs img {
  width: 180px;
}

.gallery {
  width: 50%;
}

.gallery__hero img {
  max-width: 450px;
  display: flex;
  margin: 0 auto;
  transition-duration: 4s;
}

.gallery__hero img:hover {
  transform: scale(1.2);
}

.gallery__thumbs {
  display: flex;
  justify-content: space-around;
  margin-top: 25px;
}

.gallery__thumbs img {
  max-width: 180px;
  transition: all .3s ease-in;
  border: transparent;
}

.gallery__thumbs img:hover {
  border: 1px solid #fff;
  border-radius: 4px;
}

.text--hidden {
  display: none;
}

.btn-more {
  margin-top: 20px;
  border: none;
  background: transparent;
  color: #fff;
  font-size: 18px;
  padding: 0;
  cursor: pointer;
}

.btn-more:hover {
  color: #cfc7c7;
}

.read-more-state {
  display: none;
}

.read-more-target {
  opacity: 0;
  max-height: 0;
  font-size: 0;
  transition: .25s ease;
  display: block;
}

.read-more-state:checked ~ .read-more-wrap .read-more-target {
  opacity: 1;
  font-size: inherit;
  max-height: 999em;
}

.read-more-state ~ .read-more-trigger:before {
  content: 'Ещё';
}

.read-more-state:checked ~ .read-more-trigger:before {
  content: 'Скрыть';
}

.read-more-trigger {
  cursor: pointer;
  display: inline-block;
  padding: 0 12px;
  color: #fff;
  font-size: 14px;
  line-height: 2;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-top: 15px;
}

