@media(max-width:1201px){
    .header__menu-link {
        font-size: 14px;
        margin-left: 21px;
    }
    .header__btn-login{
        padding: 8px;
    }
    .header__btn-reg{
        margin-left: 10px;
        padding: 8px;
    }
    .hero__info{
        max-width: 480px;
    }
    .hero__title{
        font-size: 36px;
    }
    .brands-inner{
        flex-direction: column;
    }
    .gallery {
        width: 100%;
    }
    .gallery__thumbs img {
        max-width: 250px;
        width: 100%;
    }
    .brands__info{
        width: 75%;
        margin: 25px auto;
    }
    .brands__content-header{
        flex-direction: column;
    }
    .brands__header-item{
        border-right: none;
        border-bottom: 1px solid #2a2a2a;
        padding: 15px 0;
        &:last-child{
            border-bottom: none;
        }
    }
    .slick-prev{
        left: 0;
    }
    .slick-next{
        right: 0;
    }
}

@media(max-width:1024px) {
    .hero__banner{
        display: none;
    }
    .hero__info {
        max-width: 600px;
        margin: auto;
    }
    .header{
        display: none;
    }
    .header__menu,
    .header-menu{
        display: none;
    }
    .header-menu{
        position: absolute;
        left: 0;
        top: 0;
        z-index: 99;
        width: 70%;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgb(0, 0, 0);
        transition: 0.5s;
        transform: translateX(-150%);
    }
    .header-menu.active{
        transform: translateX(0) !important;
        color: #fff;
        background-color: #160f18;
        display: flex;
        flex-direction: column;
    }
    .header-mobile{
        display: block;
        padding: 0;
       
    }
    .toggle-sidebar{
        display: block;
        z-index: 100;
    }
    .header-menu__list{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 100vh;
    }
    .header-menu-link {
        font-size: 26px;
        padding: 48px 0 0 0;
        transition: color .3s ease-in;
        &:hover{
            color: #e83a1c;
        }
    }
    .header__inner{
        padding: 35px 0 0 0;
    }
    .hero__inner {
        display: flex;
        justify-content: space-between;
        padding: 100px 0 50px 0;
    }
    .brands__info-title{
        font-size: 36px;
        line-height: 36px;
    }
    .brands__info-text p {
        font-size: 16px;
        line-height: 26px;
    }
}

@media(max-width:670px){
    .header-menu-link{
        font-size: 22px;
    }
    .header__btn{
        display: none;
    }
    .header__btn.header__btn-mob{
        display: block;
    }
    .header-menu.active{
        width: 100%;
    }
    .hero__title{
        font-size: 32px;
    }
    .hero__info-text{
        font-size: 18px;
    }
    .gallery__hero img {
        max-width: 320px;

    }
    .brands__info{
        width: 100%;
    }
    .brands__title{
        font-size: 36px;
        line-height: 50px;
    }
    .brands__info-title{
        font-size: 32px;
    }
}
@media(max-width:500px) {
    .footer__inner{
        flex-direction: column;
        padding: 25px 0 12px 0;
    }
    .footer__text{
        font-size: 12px;
        margin-top: 15px;
    }
}

